import React from 'react'
import Fade from 'react-reveal/Fade';

//import Slide from './slide/slide'

import './servicio.css'

const Servicio = ({ english }) => {


    return (
        <div id='servicio' className='servicio'>
            <Fade left><h1>{english ? 'Integral Service' : 'Servicio Integral'}</h1></Fade>
            <Fade right><p>{english ? 'Our strategic ally, Kapta, helps us develop a package according to your needs. It can be an adaptation flexible packaging or the creation of the brand concept' : 'Nuestro aliado estratégico Kapta, nos ayuda a desarrolla un empaque de acuerdo a tus necesidades. Puede ser una adaptación empaques flexibles o la creación del concepto de marca.'}</p></Fade>
            <span style={{width: '50%', height: 'auto'}}>
                {/* <Slide /> */}
            </span>
        </div>
    )
}

export default Servicio