import React from 'react'
import Fade from 'react-reveal/Fade';

import './nosotros.css'

const Nosotros = ({ english }) => {
    return (
        <div id='nosotros' className='nosotros'>
            <Fade left>
                <h1>{english ? 'Company' : 'Nosotros'}</h1>
            </Fade>
            <Fade right>
                <p>{english ? 'FaBru was born as a Resinplast\' division, focused on customers who don\'t have large volumes. We provide innovative package with the quality and technology that characterizes Resinplast' : 'FaBru nace como una división de Resinplast, enfocada en atender a los clientes que no tienen grandes volúmenes. Brindamos empaques novedosos e innovadores; con la tecnología y calidad que caracteriza a Resinplast.'}</p>
            </Fade>
        </div>
    )
}

export default Nosotros