import React, { Component } from 'react';

import NavBar from 'components/navbar/navbar'
import Flecha from 'components/flecha/flecha'
import Home from 'components/home/home'
import Nosotros from 'components/nosotros/nosotros'
import Sostenibilidad from 'components/sostenibilidad/sostenibilidad'
import Soluciones from 'components/soluciones/soluciones'
import Calidad from 'components/calidad/calidad'
import Servicio from 'components/servicio/servicio'
import Contacto from 'components/contacto/contacto'
import SideBar from 'components/UI/sidebar/sidebar';

import './App.css';



class App extends Component {

  state = {
    section: 0,
    english: false,
    loading: false,
    sidebar: false
  }

  componentDidMount = () => {
    window.addEventListener('scroll', (e) => {
      const scroll = e.currentTarget.scrollY;
      const height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
      const section = height / 7;
      const double = section*2;
      const triple = section*3;
      const cuatruple = section*4
      const quintuple = section*5
      if(scroll < section) {
        this.setState({
          section: 0
        })
      } else if (scroll > section && scroll < double) {
        this.setState({
          section: 1
        })
      } else if (scroll > double && scroll < triple) {
        this.setState({
          section: 2
        })
      } else if (scroll > triple && scroll < cuatruple) {
        this.setState({
          section: 3
        })
      } else if (scroll > cuatruple && scroll < quintuple) {
        this.setState({
          section: 4
        })
      } else if (scroll > quintuple) {
        this.setState({
          section: 5
        })
      }
    })
  }

  delay = (time = 500) => {
    return new Promise(resolve => {
      setTimeout(() => {
        resolve(true);
      }, time);
    });
  };

  handleLanguage = async() => {
    this.setState({
      loading: true
    })
    if(this.state.english) {
      this.setState({
        english: false
      })
    } else {
      this.setState({
        english: true
      })
    }
    await this.delay(1500)
    this.setState({
      loading: false
    })
  }

  handleSideBar = (stat) => {
    this.setState({
      sidebar: stat
    })
  }

  render() {

    const { english, loading, sidebar } = this.state;

    const app = (
      <React.Fragment>
        <SideBar sidebar={sidebar} handleSideBar={this.handleSideBar} />
        <div className="App">
          <Flecha handleLanguage={this.handleLanguage} english={english} section={this.state.section} />
          {window.innerWidth < 767 ? <NavBar handleLanguage={this.handleLanguage} english={english} mobile handleSideBar={this.handleSideBar} /> :<NavBar handleLanguage={this.handleLanguage} english={english} />}
          <Home english={english} />
          <Nosotros english={english} />
          <Sostenibilidad english={english} />
          <Soluciones english={english} />
          <Calidad english={english} />
          <Servicio english={english} />
          <Contacto english={english} />
        </div>
      </React.Fragment>
    )

    return (
      <React.Fragment>
        {loading ? <div class="spinner">
          <div class="double-bounce1"></div>
          <div class="double-bounce2"></div>
        </div> : app}
      </React.Fragment>
    );
  }
}

export default App;
