import React from 'react'

import './contacto.css'

const Contacto = ({ english }) => {
    return (
        <div id='contacto' className='contacto'>
            <h2>{english ? 'Ready to start?' : '¿Listo para comenzar?'}</h2>
            <div className='form'>
                <label>{english? 'Name' : 'Nombre'}</label>
                <input name='nombre'  type='text' />
                <label>Email</label>
                <input name='email' type='text' />
                <label>{english ? 'Business' : 'Empresa'}</label>
                <input name='empresa'  type='text' />
                <label>{english ? 'Phone' : 'Telefono'}</label>
                <input name='telefono' type='text' />
                <label>Mensaje</label>
                <textarea name='mensaje' rows='10'></textarea>
                <br />
                <button className='submitButton'>{english ? 'Submit' : 'Enviar'}</button>
            </div>
        </div>
    )
}

export default Contacto