import React from 'react'
import Fade from 'react-reveal/Fade';

import solu from '../assets/img/sol.gif'

import './soluciones.css'

const Soluciones = ({ english }) => {
    return (
        <div id='soluciones' className='soluciones'>
            <Fade right><img src={solu} alt='soluciones' /></Fade>
            <Fade left><h1>{english ?  'Solutions' : 'Soluciones'}</h1></Fade>
            <Fade right><p>{english ? 'Our packages can be purchased from 1000 units. We offer labels, coils, or pre-assembled bags (doypacks)' : 'Nuestros empaques se pueden adquirir a partir de 1000 unidades. Ofrecemos etiquetas, bobinas, o bolsas pre armadas (doypacks)'}</p></Fade>
        </div>
    )
}

export default Soluciones