import React from 'react'
import Fade from 'react-reveal/Fade';

import cali from '../assets/img/cal.gif'

import './calidad.css'

const Calidad = ({ english }) => {
    return (
        <div id='calidad' className='calidad'>
            <Fade right><img src={cali} alt='calidad' /></Fade>
            <Fade left><h1>{english ? 'Quality' : 'Calidad'}</h1></Fade>
            <Fade right><p>{english ? 'We have more than 30 years in the field, with all contact with food necessary certificacions. You can know more about Resinplast clicking' : 'Tenemos más de 30 años en el rubro, contando con todas las certificaciones necesarias para contacto con alimentos. Puedes conocer un poco más de Resinplast haciendo click'} <a href='http://anthonyblas.com/muestra/#'>{english ? 'here' : 'aquí'}</a>.</p></Fade>
        </div>
    )
}

export default Calidad