import React from 'react'

import BackgroundSlider from 'react-background-slider'

import b1 from '../assets/img/S1.png'
import b2 from '../assets/img/S2.png'
import b3 from '../assets/img/S3.png'
import b4 from '../assets/img/S4.png'
import b5 from '../assets/img/S5.png'


import logo from 'components/assets/img/logo.png'
import './home.css'

const Home = ({ english }) => {
    return(
        <React.Fragment>
        <div id='home' className='home'>
            <div className='logo-header'>
                <img src={logo} alt='logo' />
                <h1>{english ? 'GIVE YOUR PRODUCT THE PACKAGE THAT DESERVE' : 'DALE A TU PRODUCTO EL EMPAQUE QUE SE MERECE'}</h1>
            </div>
            <BackgroundSlider
            images={[b1, b2, b3, b4, b5]}
            duration={5} transition={1} />
        </div>
        </React.Fragment>
    )
}

export default Home