import React from 'react'



import BackDrop from '../backdrop/backdrop'

import logo from 'components/assets/img/logo.png'

import './sidebar.css'

const SideBar = ({ english, handleSideBar, sidebar }) => {
    return (
        <React.Fragment>
                <div id='sidenav' className={sidebar ? 'show' : 'hide'}>
                <img src={logo} alt='logo' onClick={() => {
                    document.documentElement.scrollTop = 0;
                }} style={{cursor: 'pointer'}} />
                <a href="#nosotros">{english ? 'Company' : 'Nosotros'}</a>
                <a href="#sostenibilidad">{english ? 'Sustainability' : 'Sostenibilidad'}</a>
                <a href="#soluciones">{english ? 'Solutions' : 'Soluciones'}</a>
                <a href="#calidad">{english ? 'Quality': 'Calidad'}</a>
                <a href="#servicio">{english ? 'Integral Service': 'Servicio integral'}</a>
                <a href="#contacto">{english ? 'Contact Us' : 'Contacto'}</a>
                </div>
                {sidebar ? <BackDrop handleSideBar={handleSideBar} /> : null}
        </React.Fragment>
    )
}


export default SideBar