import React, { Component } from "react";

import logo from "../assets/img/logo.png";

import "./navbar.css";

class NavBar extends Component {
  state = {
    isTop: false
  };

  componentDidMount = () => {
    window.addEventListener("scroll", this.handleScroll);
  };

  componentWillUnmount = () => {
    window.removeEventListener('scroll', this.handleScroll)
  }

  handleScroll = () => {
    const isTop = window.scrollY >= 350;
    if (isTop !== this.state.isTop) {
      this.setState({ isTop });
    }
  }

  render() {
    const { mobile, english, handleSideBar } = this.props;
    const { isTop } = this.state;

    return (
      <React.Fragment>
        <div className="navbar">
            <img
              id='img'
              className={isTop ? 'show' : 'hide'}  
              src={logo}
              alt="logo"
              onClick={() => {
                document.documentElement.scrollTop = 0;
              }}
              style={{ cursor: "pointer" }}
            />
          <ul>
            {mobile ? (
              <React.Fragment>
                {/* <span
                  style={{
                    display: "flex",
                    color: "white",
                    marginRight: "15px",
                    marginTop: "10%"
                  }}
                  onClick={handleLanguage}
                >
                  {english ? "Español" : "English"}
                </span> */}
                <div className="nav-icon" onClick={() => handleSideBar(true)}>
                  <div />
                </div>
              </React.Fragment>
            ) : (
              <React.Fragment>
                <li>
                  <a href="#nosotros">{english ? "Company" : "Nosotros"}</a>
                </li>
                <li>
                  <a href="#sostenibilidad">
                    {english ? "Sustainability" : "Sostenibilidad"}
                  </a>
                </li>
                <li>
                  <a href="#soluciones">
                    {english ? "Solutions" : "Soluciones"}
                  </a>
                </li>
                <li>
                  <a href="#calidad">{english ? "Quality" : "Calidad"}</a>
                </li>
                <li>
                  <a href="#servicio">
                    {english ? "Integral Service" : "Servicio integral"}
                  </a>
                </li>
                <li>
                  <a href="#contacto">{english ? "Contact Us" : "Contacto"}</a>
                </li>
              </React.Fragment>
            )}
          </ul>
        </div>
      </React.Fragment>
    );
  }
}

export default NavBar;
