import React from 'react'

import flecha from '../assets/img/flecha.png'

import './flecha.css'

const Flecha = ({ section, handleLanguage, english, nav }) => {   
    const handleClick = () => {
        if(section === 0) {
            window.location.href='#nosotros'
        } else if (section === 1) {
            window.location.href='#sostenibilidad'
        } else if (section === 2) {
            window.location.href='#soluciones'
        } else if (section === 3) {
            window.location.href='#calidad'
        } else if (section === 4) {
            window.location.href='#servicio'
        } else if (section === 5) {
            window.location.href='#contacto'
        }
    }

    return (
        <div className='flecha'>
            {/* <span className='language' onClick={handleLanguage}>{english ? 'Español' : 'English'}</span> */}
            <img src={flecha} alt='flecha' onClick={handleClick} />
        
        </div>
    )
}

export default Flecha