import React from 'react'
import Fade from 'react-reveal/Fade';

import eco from '../assets/img/sost.gif'

import './sostenibilidad.css'

const Sostenibilidad = ({ english }) => {
    return (
        <div id='sostenibilidad' className='sostenibilidad'>
            <Fade right><img src={eco} alt='sostenibilidad' /></Fade>
            <Fade left><h1>{english ? 'Sustainability' : 'Sostenibilidad'}</h1></Fade>
            <Fade right><p>{english ? 'We are an eco = responsible company that seeks to take care of the environment, that is how we implement plans to reduce the generation of solid waste. We also offer 100% OXO biodegradable and PLA packaging.' : 'Somos una empresa eco=responsable que busca cuidar el medio ambiente, es así que implementamos planes para reducir la generación de desechos sólidos y reciclar al 100% los desperdicios. También ofrecemos empaques 100% OXO biodegradables y de PLA.'}</p></Fade>
        </div>
    )
}

export default Sostenibilidad